.midia {
  padding: 0 100px 0 100px;
  background-color: #f2f2f2;
  width: 100%;
  overflow-x: hidden;

}
.midia-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}
.midia-title {
    font-size: 3em;
    text-align: center;
    padding: 90px 0;
    color:#444649;
    text-decoration: underline;
  }
.videos-subtitle {
    font-size: 1.3em;
    color:rgb(110, 9, 9);
    font-weight: 600;
  }
.videos {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
  }
.videos-text{
      font-size: 16px;
      color:#444649;
      font-weight: 600;
      padding: 30px 0 20px 0;
  }
.videos-content { 
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
    }
.news {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
    margin-left: 50px;
    padding: 0 40px;
  }
.news-subtitle {
      font-size: 1.3em;
      padding-bottom: 65px;
      color:rgb(110, 9, 9);
      font-weight: 600;
    }
.news-content {
      font-size: 18px;
      margin-bottom: 30px;
      padding: 30px;
      color:#444649;
      /* margin-left: 70px; */
      /* border-bottom: 0.5px solid lightgrey; */
      box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.3), 0 1px 3px 1px rgba(60, 64, 67, 0.15);
    }
