@media only screen and (max-width: 420px) {
  .midia {
    padding: 0;
  }
  .midia-title {
    font-size: 24px;
    padding: 20px 0 10px 10px;
    margin-left: 10px;
    color: #444649;
    text-align: left;
    text-align: initial;
    font-weight: bold;
  }
  .midia-content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    overflow: hidden;
  }
  .news-subtitle,
  .videos-subtitle {
    font-size: 18px;
    padding: 20px 0 10px 20px;
    font-weight: 600;
  }
  .news {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
    margin: 10px;
    margin-left: 0;
    padding: 0;
    
  }
    .news-content {
      /* padding: 20px 0 0 20px; */
      margin: 20px;
      color:#444649;
      border-bottom: 0.5px solid lightgrey;
    }
    .canal-youtube--text {
      padding: 0 0 10px 20px;
    }
    .canal-youtube {
      margin: 0 10px 30px 10px;
    }
