@media only screen and (max-width: 780px) {
  .midia {
    padding: 0;
    width: 100%;
  }
  .midia-title {
    font-size: 22px;
    padding: 20px 0 10px 10px;
    margin-left: 10px;
    color: #444649;
    text-align: left;
    text-align: initial;
    font-weight: bold;
  }
  .midia-content {
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
    -webkit-flex-direction: column-reverse;
            flex-direction: column-reverse;
    overflow: hidden;
  }
  .midia-subtitle {
    font-size: 16px;
    padding: 20px 0 10px 20px;
    color:#444649;
    font-weight: 600;
  }
  .news-subtitle,
  .videos-subtitle {
    font-size: 18px;
    padding: 20px 0 10px 20px;
    font-weight: 600;
  }
  .news {
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
    margin: 10px;
    margin-left: 0;
    padding: 0;
    
  }
    .news-content {
      padding: 20px 0 10px 20px;
      margin: 15px;
      color:#444649;
      border-bottom: 0.5px solid lightgrey;
    }
    .canal-youtube--text {
      padding: 0 0 10px 20px;
     
    }
    .canal-youtube {
      margin: 0 10px 30px 10px;
    }
    .video {
      margin-left: 20px;
    }
    .videos-text {
      margin-left: 20px;
      padding: 10px 0 10px 0;
    }
    .videos-content {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
    }
  }